import { Component, OnInit } from "@angular/core";
import { DatePipe } from "@angular/common";
import { HttpErrorResponse } from "@angular/common/http";
import * as $ from "jquery";
import { CreateQuoteService } from 'src/app/core/services/create-quote-service';
import { CommonService } from 'src/app/core/services/common-service';
@Component({
  // selector: 'app-create-quote',
  templateUrl: "./file-reupload.component.html",
  styleUrls: ["./file-reupload.component.css"],
})
export class FileReUploadComponent implements OnInit {
  lobProductList: any;
  token: any;
  lobList: any;
  lobCode: string;
  fileToUpload: File = null;
  msg: string="No file chosen";
  sucesserror: string="";
  batchid: string;
  constructor(
  public datepipe: DatePipe,
  private createQuoteService: CreateQuoteService, private commonService: CommonService)
  {
    
  }

  ngOnInit() 
  {
    
  }
  
  handleFileInput(files: FileList) 
  {
    this.fileToUpload = files.item(0);
    this.msg = this.fileToUpload.name;
  }

  fileRetryUpload()
  {
    // if(this.batchid == "" || this.batchid == "undefined")
    // {
    //   this.sucesserror = 'Please enter BatchID';
    //   $("#AlertModal").show();
    // }
    var fileExt = this.fileToUpload.name.substr(this.fileToUpload.name.lastIndexOf('.') + 1);
    if(fileExt != "xlsx")
    {
      this.msg ="No file chosen";
      this.sucesserror = 'Please upload excel file';
      $("#AlertModal").show();
    }
    let formData = new FormData();
    formData.append('file', this.fileToUpload);
    this.createQuoteService
    .retryuploadFile(formData)
    .subscribe(
    (res) => 
    {
      console.log(res);
      if(res != "")
      {
        this.msg ="No file chosen";
        this.sucesserror ='File uploaded sucessfully';
        $("#AlertModal").show();
      }
    },
    (error) => () => 
    {

    }
  );}
}
